/*
 
*/

import vue from 'vue';
import vueScroll from 'vuescroll';
import vueTouchEvents from 'vue2-touch-events';
import vueRouter from 'vue-router';
import vuetify from 'vuetify/lib'; //Material Design   Vue UI  . https://vuetifyjs.com/en/getting-started/installation/   .





/*
   
*/

//       ,    global      window.library     .
//*     import-glob webpack preloader npm  
//https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Statements/import#import_a_module_for_its_side_effects_only

import "./libraries/cdn/gsap-custom-ease.js"; import "./libraries/cdn/iamport.js"; import "./libraries/cdn/jquery.js"; import "./libraries/custom/become-new.js"; import "./libraries/custom/calculate-position.js"; import "./libraries/custom/detect-mobile.js"; import "./libraries/custom/hex-to-rgb.js"; import "./libraries/custom/rgb-to-hex.js"; import "./libraries/custom/simple-pay.js"; import "./libraries/npm/axios.js"; import "./libraries/npm/gsap.js";





/*
  
*/

import app from './app.vue'
import './style.css';






/*
Vue  
*/

vue.use(vueRouter)
vue.use(vueTouchEvents);
vue.use(vueScroll, {
  ops: {
    vuescroll: {
      mode: 'native',
      sizeStrategy: 'percent',
      detectResize: true,
      wheelScrollDuration: 100,
      wheelDirectionReverse: false
    },
    scrollPanel: {
      scrollingX: false,
      scrollingY: true,
    },
    rail: {},
    bar: {
      showDelay: 1000,
      onlyShowBarOnScroll: true,
      keepShow: false,
      background: '#000000',
      opacity: 0.2,
      hoverStyle: false,
      specifyBorderRadius: false,
      minSize: 0,
      size: '6px',
      disable: false
    }
  },
  name: 'vue-scroll'
});
vue.use(vuetify);





/*
Vue  
*/

//     v-guide-alert 
vue.directive('guide-alert', {
  //    DOM  ...
  inserted: function (el, binding, vnode) {
    let showTimer;

    el.addEventListener('mouseover', function () {
      showTimer = setTimeout(function () {
        vnode.context.$root.showElasticAlert(el, binding.value);
      }, 500)
    })
    el.addEventListener('mouseleave', function () {
      clearTimeout(showTimer);
    })
    el.addEventListener('mousedown', function () {
      clearTimeout(showTimer);
    })
    el.addEventListener('click', function () {
      clearTimeout(showTimer);
    })
  }
})





/*
Vue  
*/

import helloWorld from './components/hello-world.vue'
vue.component('hello-world', helloWorld)
import topBar from './components/top-bar.vue';
vue.component('top-bar', topBar)
import elasticButton from './components/elastic-button.vue';
vue.component('elastic-button', elasticButton)
import elasticTextbox from './components/elastic-textbox.vue';
vue.component('elastic-textbox', elasticTextbox)
import elasticAlert from './components/elastic-alert.vue';
vue.component('elastic-alert', elasticAlert)
import divider from './components/divider.vue';
vue.component('divider', divider)
import layoutBlock from './components/layout-block.vue';
vue.component('layout-block', layoutBlock)







/*
Vue  
*/

vue.config.productionTip = false





/*
  (=HTML )
*/

new vue({
  vuetify: new vuetify(),
  render: h => h(app),
}).$mount('#vueModelElement')